<template>
  <div id="note-detail">
    <head-top title="笔记详情"></head-top>
    <div class="main">
      <div class="tt">{{title || '关于中美贸易战的影响'}}</div>
      <pre v-if="contentType === contentTypeDefault" class="content">{{content || '中美贸易战间接影响深远，摩根士丹利估计全球贸易可能受到严重破坏，因三分之二的贸易商品与全球价值链相关联。'}}</pre>
      <img v-else :src=" getImageFile(content)" alt />
    </div>
  </div>
</template>

<script>
import headTop from "@/components/headTop";

import { ContentType, ModulePicture } from "@/utils/enum";
export default {
  name: "",
  components: {
    headTop
  },
  props: {},
  data() {
    return {
      title: this.$route.query.title || "",
      content: this.$route.query.content || "",
      contentType: this.$route.query.type || "",
      contentTypeDefault: ContentType.Text,
      img: this.$route.query.img || ""
    };
  },
  created() {},
  computed: {},
  watch: {},
  methods: {
    getImageFile(fileId) {
      return this.$gatewayHttp.resourse.picture(
        fileId,
        ModulePicture.StudentNote
      );
    }
  }
};
</script>

<style lang='less' scoped>
#note-detail {
  width: 100%;
  height: 100%;
  background: #fff;
  .main {
    padding: 4vw;
    width: 92vw;
    text-align: left;
    .tt {
      font-size: 1.4rem;
      color: rgba(51, 51, 51, 1);
      margin-top: 20px;
      margin-bottom: 15px;
    }
    .content {
      font-size: 1.2rem;
      color: rgba(102, 102, 102, 1);
      margin-bottom: 10px;

      white-space: pre-wrap;
      white-space: -moz-pre-wrap;
      white-space: -pre-wrap;
      white-space: -o-pre-wrap;
    }

    img {
      max-width: 90vw;
      max-height: 80vh;
    }
  }
}
</style>


